<template>
    <div class="charts">
      <div class="container-charts">
        <ve-pie :data="chartData" :settings="chartSettings"></ve-pie>
      </div>
      <div class="container-table">
        <el-table
          row-class-name="row"
          :data="tableData"
          style="width: 100%">
          <el-table-column
            prop="fileType"
            label="文件类型"
            >
          </el-table-column>
          <el-table-column
            prop="rollCount"
            label="总卷数"
            >
          </el-table-column>
          <el-table-column
            prop="fileCount"
            label="总页数">
          </el-table-column>
        </el-table>
      </div>
    </div>
</template>

<script>
  import {statistics} from 'api/charts'
  export default {
    name: 'charts',
    created () {
      this._statistics()
    },
    methods: {
      _statistics () {
        statistics().then(res => {
          this.tableData = res.data.data.typeStatistics
          this.chartData.rows = res.data.data.typeStatistics
        })
      }
    },
    data () {
      this.chartSettings = {
        legendName: {
          'profit': 'haha'
        }
      }
      return {
        tableData: [],
        chartData: {
          columns: ['fileType', 'rollCount', 'fileCount'],
          rows: []
        }
      }
    }
  }
</script>

<style scoped lang="stylus">
  .charts
    display flex
    .container-table
    .container-charts
      flex 1
</style>



